'use client';

import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from './ui/Button';
import { User, LogOut } from 'lucide-react';

export function TopBar() {
    const { user, logout } = useAuth();

    return (
        <header className="fixed top-0 left-0 right-0 h-16 bg-card border-b z-50">
            <div className="h-full max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="h-full flex items-center justify-between">
                    {/* Logo */}
                    <Link href="/" className="text-xl font-bold text-foreground">
                        Linguex
                    </Link>

                    {/* Navigation */}
                    <nav className="flex items-center space-x-4">
                        <Link href="/materials" className="text-muted-foreground hover:text-foreground">
                            Materials
                        </Link>
                        <Link href="/friends" className="text-muted-foreground hover:text-foreground">
                            Friends
                        </Link>
                        <Link href="/profile" className="text-muted-foreground hover:text-foreground">
                            Profile
                        </Link>
                    </nav>

                    {/* User Menu */}
                    <div className="flex items-center space-x-4">
                        {user ? (
                            <div className="flex items-center space-x-4">
                                <span className="text-sm text-muted-foreground">
                                    {user.fullName}
                                </span>
                                <Button
                                    variant="ghost"
                                    size="icon"
                                    onClick={logout}
                                    className="text-muted-foreground hover:text-foreground"
                                >
                                    <LogOut className="h-5 w-5" />
                                </Button>
                            </div>
                        ) : (
                            <Link href="/sign-in">
                                <Button variant="ghost" size="icon">
                                    <User className="h-5 w-5" />
                                </Button>
                            </Link>
                        )}
                    </div>
                </div>
            </div>
        </header>
    );
} 